.card {
  position: relative;
  width: 100%;
  padding: 8px;
  background: var(--grey-300);
  border-top: 1px solid var(--grey-200);
  border-radius: 16px;
  box-shadow: var(--shadow-button);
  transition: all 0.3s ease-out;
}
@media screen and (min-width: 1280px) {
  .card {
    padding: 8px 8px 16px;
  }
}
.card:hover {
  background: var(--grey-200);
  border-top: 1px solid var(--grey);
  border-radius: 16px;
  box-shadow: 0 4px 16 0 rgba(0, 0, 0, 0.1), 0 8px 10px -6px rgba(0, 0, 0, 0.05), 0 20px 25px -5px rgba(0, 0, 0, 0.05);
  cursor: pointer;
}
.card__number {
  position: absolute;
  top: 16px;
  left: 16px;
  color: var(--white);
  font-size: calc(8px + ( 12 - 8 ) * ( (100vw - 320px) / ( 1728 - 320) ));
  line-height: calc(8px + ( 14.4 - 8 ) * ( (100vw - 320px) / ( 1728 - 320) ));
}
@media screen and (min-width: 1280px) {
  .card__number {
    top: 20px;
    left: 20px;
  }
}
.card__container-photo {
  display: flex;
  justify-content: center;
  padding: 16px;
  background: var(--black-100);
  border-radius: 8px;
}
@media screen and (min-width: 1280px) {
  .card__container-photo {
    padding: 40px;
  }
}
.card__photo {
  width: 100%;
  max-width: 240px;
  max-height: 240px;
  aspect-ratio: 1/1;
}
.card__info {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 8px 0 16px;
}
@media screen and (min-width: 1280px) {
  .card__info {
    flex-wrap: nowrap;
    justify-content: space-between;
    padding: 16px 8px 24px;
  }
}
.card__name {
  width: 100%;
  margin: 0;
  overflow: hidden;
  color: var(--white);
  font-size: calc(16px + ( 32 - 16 ) * ( (100vw - 320px) / ( 1728 - 320) ));
  line-height: calc(19.84px + ( 39.68 - 19.84 ) * ( (100vw - 320px) / ( 1728 - 320) ));
  letter-spacing: calc(0.32px + ( 0.64 - 0.32 ) * ( (100vw - 320px) / ( 1728 - 320) ));
  white-space: nowrap;
  text-align: center;
  text-overflow: ellipsis;
}
@media screen and (min-width: 1280px) {
  .card__name {
    width: auto;
    text-align: left;
  }
}
.card__type {
